/* Styles for AuthForm component */
.auth-container {
  position: relative;
  width: 400px;
  min-height: 400px;
  margin: 2rem auto;
  padding: 2rem;
  background: #1a1a1a;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  font-family: 'Arial', sans-serif;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Forms styling */
.auth-form {
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
  position: relative;
  transition: opacity 0.6s;
  opacity: 1;
  background: transparent;
  box-shadow: none;
}
.auth-form h2 {
  text-align: center;
  margin-bottom: 1rem;
  color: #fff;
}
.auth-form label {
  display: block;
  margin-bottom: 0.3rem;
  color: #ccc;
}
.auth-form input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #333;
  border-radius: 4px;
  background: #2a2a2a;
  color: #fff;
}
.auth-form input::placeholder {
  color: #777;
}
.auth-form input:focus {
  outline: none;
  border-color: #00a6ff;
}
.auth-form button {
  width: 100%;
  padding: 0.7rem;
  margin-top: 0.5rem;
  background: #00a6ff;
  border: none;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}
.auth-form button:hover {
  background: #008ecc;
}
.auth-form .error {
  color: #ff6b6b;
  font-size: 0.9rem;
  text-align: center;
  margin-bottom: 1rem;
}
/* Toggle link text */
.auth-form .toggle-text {
  margin-top: 1rem;
  text-align: center;
  color: #bbb;
  font-size: 0.9rem;
}
.auth-form .toggle-text .toggle-link {
  color: #00a6ff;
  cursor: pointer;
}
.auth-form .toggle-text .toggle-link:hover {
  text-decoration: underline;
}

/* Positioning of login and signup forms */
.login-form {
  display: none;
}
.signup-form {
  display: none;
}
.auth-container.login-mode .login-form {
  display: block;
}
.auth-container.signup-mode .signup-form {
  display: block;
}

/* Responsive adjustments */
@media (max-width: 500px) {
  .auth-container {
    width: 100%;
    margin: 1rem;
    padding: 1.5rem;
  }
}

/* Add to bottom of file */
.auth-container {
  width: 100%;
  height: 100%;
  padding: 2rem 1.5rem;
  overflow: auto;
}

.auth-form {
  max-width: 100%;
  padding-top: 1rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .hero-section {
    flex-direction: column;
  }
  
  .hero-media {
    width: 100%;
    margin-top: 2rem;
    height: 400px;
  }
  
  .data-buttons-grid {
    grid-template-columns: 1fr;
  }
}