@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  --color-primary: #0af;
  --color-secondary: #0f5;
  --color-accent: #ff4d94;
  --bg-dark: #0a0a14;
  --bg-dark-alt: #161626;
  --bg-light: #f9f9f9;
  --card-bg: rgba(30, 30, 46, 0.6);
  --text-light: #e0e0e0;
  --text-lighter: #f5f5f7;
  --text-dark: #333333;
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  
  --gradient-primary: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
  --gradient-accent: linear-gradient(135deg, var(--color-accent), #ff9a9e);
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-border: rgba(255, 255, 255, 0.1);
  
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 4rem;
  --spacing-xl: 6rem;
  
  --border-radius: 12px;
  --border-radius-lg: 24px;
  
  --shadow-sm: 0 4px 12px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 8px 24px rgba(0, 0, 0, 0.2);
  --shadow-lg: 0 16px 48px rgba(0, 0, 0, 0.3);
  
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  background-color: var(--bg-dark);
  color: var(--text-light);
  overflow-x: hidden;
}

.landing-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header */
.header {
  background-color: transparent;
  padding: var(--spacing-sm) var(--spacing-md);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(10px);
  background-color: var(--glass-bg);
  border-bottom: 1px solid var(--glass-border);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-lighter);
}

.logo-gradient {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: var(--spacing-md);
}

.nav-item {
  text-decoration: none;
  color: var(--text-light);
  font-weight: 500;
  position: relative;
  padding: 8px 0;
  transition: var(--transition);
}

.nav-item::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--gradient-primary);
  transition: var(--transition);
}

.nav-item:hover {
  color: var(--text-lighter);
}

.nav-item:hover::after {
  width: 100%;
}

.auth-buttons {
  display: flex;
  gap: var(--spacing-sm);
}

.login-button,
.signup-button {
  background-color: transparent;
  border: 2px solid var(--color-primary);
  color: var(--color-primary);
  padding: 8px 20px;
  cursor: pointer;
  border-radius: var(--border-radius);
  font-weight: 600;
  transition: var(--transition);
  font-size: 0.95rem;
}

.login-button:hover,
.signup-button:hover {
  background-color: var(--color-primary);
  color: var(--bg-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.signup-button {
  background: var(--gradient-primary);
  color: var(--bg-dark);
  border: none;
}

.signup-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(0, 170, 255, 0.4);
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: var(--spacing-md);
}

/* Hero Section */
.hero-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: var(--spacing-lg) auto;
  gap: var(--spacing-xl);
}

.hero-content {
  flex: 1;
  max-width: 600px;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: var(--spacing-md);
  color: var(--text-lighter);
}

.hero-highlight {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
}

.hero-description {
  font-size: 1.25rem;
  line-height: 1.6;
  margin-bottom: var(--spacing-md);
  color: var(--text-light);
  opacity: 0.9;
}

.cta-container {
  display: flex;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
}

.cta-primary,
.cta-secondary {
  padding: 14px 32px;
  border-radius: var(--border-radius);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  border: none;
}

.cta-primary {
  background: var(--gradient-primary);
  color: var(--bg-dark);
  box-shadow: var(--shadow-sm);
}

.cta-primary:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-md);
}

.cta-secondary {
  background: transparent;
  color: var(--text-lighter);
  border: 2px solid var(--glass-border);
}

.cta-secondary:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-3px);
}

.hero-media {
  flex: 1;
  position: relative;
  height: 500px;
}

.media-placeholder {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius-lg);
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-shadow: var(--shadow-lg);
}

.animated-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(60px);
  z-index: -1;
  animation: float 12s infinite ease-in-out;
}

.circle-1 {
  width: 300px;
  height: 300px;
  background: var(--color-primary);
  opacity: 0.15;
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.circle-2 {
  width: 250px;
  height: 250px;
  background: var(--color-accent);
  opacity: 0.15;
  bottom: 10%;
  right: 10%;
  animation-delay: 2s;
}

.circle-3 {
  width: 200px;
  height: 200px;
  background: var(--color-secondary);
  opacity: 0.15;
  top: 40%;
  left: 50%;
  animation-delay: 4s;
}

.particle {
  position: absolute;
  border-radius: 50%;
  background: var(--gradient-primary);
  opacity: 0.7;
  filter: blur(20px);
  animation: pulse 4s infinite alternate;
}

.particle-1 {
  width: 200px;
  height: 200px;
  top: 20%;
  left: 20%;
  animation-delay: 0s;
}

.particle-2 {
  width: 150px;
  height: 150px;
  bottom: 30%;
  right: 20%;
  animation-delay: 1s;
}

.particle-3 {
  width: 100px;
  height: 100px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation-delay: 2s;
}

/* Features Section */
.features-section {
  max-width: 1400px;
  margin: var(--spacing-xl) auto;
  padding: var(--spacing-lg) 0;
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: var(--spacing-xl);
  color: var(--text-lighter);
}

.text-gradient {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
}

.feature-card {
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius);
  padding: var(--spacing-lg);
  transition: var(--transition);
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient-primary);
  opacity: 0;
  z-index: -1;
  transition: var(--transition);
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg);
}

.feature-card:hover::before {
  opacity: 0.1;
}

.feature-card h3 {
  font-size: 1.5rem;
  margin: var(--spacing-sm) 0;
  color: var(--text-lighter);
}

.feature-card p {
  color: var(--text-light);
  opacity: 0.8;
  line-height: 1.6;
}

.feature-icon {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto var(--spacing-sm);
}

.icon-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--gradient-primary);
  opacity: 0.1;
  border-radius: 50%;
}

.feature-icon span {
  font-size: 2.5rem;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

/* Footer */
.footer {
  background-color: var(--bg-dark-alt);
  padding: var(--spacing-xl) var(--spacing-md) var(--spacing-sm);
}

.footer-content {
  max-width: 1400px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-xl);
  padding-bottom: var(--spacing-lg);
}

.footer-brand {
  max-width: 350px;
}

.footer-tagline {
  margin-top: var(--spacing-sm);
  color: var(--text-light);
  opacity: 0.7;
  line-height: 1.6;
}

.footer-links {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--spacing-lg);
}

.link-group {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.link-group h4 {
  color: var(--text-lighter);
  margin-bottom: var(--spacing-xs);
  font-size: 1.1rem;
}

.link-group a {
  color: var(--text-light);
  text-decoration: none;
  opacity: 0.7;
  transition: var(--transition);
}

.link-group a:hover {
  opacity: 1;
  color: var(--color-primary);
}

.footer-bottom {
  max-width: 1400px;
  margin: 0 auto;
  padding-top: var(--spacing-lg);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: var(--spacing-sm);
}

.footer-bottom p {
  color: var(--text-light);
  opacity: 0.6;
  font-size: 0.9rem;
}

.legal-links {
  display: flex;
  gap: var(--spacing-sm);
  align-items: center;
}

.legal-links a {
  color: var(--text-light);
  text-decoration: none;
  opacity: 0.6;
  font-size: 0.9rem;
  transition: var(--transition);
}

.legal-links a:hover {
  opacity: 1;
  color: var(--color-primary);
}

.separator {
  opacity: 0.3;
}

/* Animations */
@keyframes float {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(20px, 20px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1.2);
    opacity: 0.3;
  }
}

/* Responsive layout */
@media (max-width: 1024px) {
  .hero-section {
    flex-direction: column;
    gap: var(--spacing-md);
    text-align: center;
  }
  
  .cta-container {
    justify-content: center;
  }
  
  .hero-media {
    width: 100%;
    max-width: 600px;
    margin-top: var(--spacing-lg);
  }
  
  .hero-title {
    font-size: 2.8rem;
  }
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    gap: var(--spacing-sm);
  }
  
  .nav-list {
    justify-content: center;
    margin-top: var(--spacing-sm);
  }
  
  .hero-title {
    font-size: 2.3rem;
  }
  
  .hero-description {
    font-size: 1.1rem;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .nav-list {
    flex-direction: column;
    gap: 0;
    text-align: center;
  }
  
  .auth-buttons {
    width: 100%;
    justify-content: center;
    margin-top: var(--spacing-sm);
  }
  
  .login-button, .signup-button {
    width: 100%;
  }
  
  .cta-container {
    flex-direction: column;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .footer-bottom {
    flex-direction: column;
    text-align: center;
  }
  
  .legal-links {
    margin-top: var(--spacing-sm);
  }
}
/* Add to bottom of file */
.data-buttons-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 1rem;
  text-align: center;
}

.data-buttons-container h3 {
  margin-bottom: 1.5rem;
  color: var(--text-lighter);
}

.data-buttons-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  width: 100%;
}

.data-button {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius);
  padding: 1.2rem 0.5rem;
  color: var(--text-lighter);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.data-button:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-3px);
}

.data-button span {
  font-size: 1.8rem;
}

.data-content-container {
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  overflow-y: auto;
  position: relative;
}

.back-button {
  position: absolute;
  top: 15px;
  left: 15px;
  background: transparent;
  border: none;
  color: var(--color-primary);
  cursor: pointer;
  font-weight: 600;
  z-index: 10;
}

.back-button:hover {
  text-decoration: underline;
}

.auth-container-inline {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 2.5rem;
}

.loading-spinner {
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top: 4px solid var(--color-primary);
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 2rem auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message {
  color: #ff6b6b;
  text-align: center;
  margin-top: 2rem;
}

.weather-data, .quote-data, .news-data {
  margin-top: 1.5rem;
  text-align: center;
}

.quote-data blockquote {
  font-style: italic;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.image-data {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.image-data img {
  max-width: 100%;
  border-radius: 8px;
}